ol.anatomy-weapon-list {
    padding-left: 0px;
}

li.anatomy-weapon {
    list-style-type: none;
}

li.anatomy-weapon:not(:last-child) {
    margin-bottom: 3px;
}

.anatomy-weapon-summary {
    display: grid;
    grid-template-columns: repeat(10, 1fr);
    border: 1px solid;

    div.image-container {
        grid-column: 1 / 2;
        height: 50px;
        align-items: center;
        display: flex;
        border-right: 1px black solid;

        img {
            height: 50px;
            width: auto;
            border: none;
        }
    }

    div.name {
        grid-column: 2 / 10;
        height: 50px;
        line-height: 50px;
        text-align: center;
        background: rgba(0, 0, 0, 0.05);

        span {
            display: inline-block;
            vertical-align: middle;
            line-height: normal;
        }
    }

    a {
        grid-column: 10 / 11;
        display: flex;
        align-items: center;
        justify-content: center;

        i {
            display: block;
        }
    }
}